body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media only screen and (max-width: 992px) {
  .project-page {
    background: rgb(0, 0, 0);
    height: 100%;
    background-size: 100vw;
    z-index: 0;
  }

  .blog-page {
    background: rgb(136, 2, 2);
    height: 100%;
    background-size: 100vw;
    z-index: 0;
  }

  .home-page{
    background: rgb(1, 0, 59);
    height: 100%;
    background-size: 100vw;
    z-index: 0;
  }

  .skill-section{
    width: 100%;
    font-size: 1.2em;
    color: white;
    margin-top: 50px;
  }

  .down-shifted {
    margin: 50px;
  }

  .centered{
    text-align: center;
  }
}

@media only screen and (min-width: 991px) {
  .project-page {
    background: url(https://i.imgur.com/6dl147q.jpg) no-repeat center center fixed;
    -webkit-background-size: 100%;
    -moz-background-size: 100%;
    -o-background-size: 100%;
    height: 65vw;
    background-size: 100%;
    z-index: 0;
  }

  .blog-page {
    background: url(https://i.imgur.com/DzLXHgN.jpg) no-repeat center center fixed;
    -webkit-background-size: 100%;
    -moz-background-size: 100%;
    -o-background-size: 100%;
    height: 65vw;
    background-size: 100%;
    z-index: 0;
  }

  .home-page{
    background: rgb(1, 0, 59);
    height: 65vw;
    background-size: 100%;
    z-index: 0;
  }

  .skill-section{
    width: 45%;
    font-size: 1.2em;
    color: white;
    float: right;
    margin-top: 50px;
  }

  .column{
    width: 25%;
    float: left;
  }

  .down-shifted {
    margin: 50px;
  }

  .centered{
    text-align: center;
  }
}

.blog-link a{
  color: rgb(224, 255, 87);
}

.blog-link a:hover{
  color: rgb(224, 255, 87);
}
.navigation {
  height: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  background-color: #000000;
  position: fixed; /* Set the navbar to fixed position */
  top: 0; /* Position the navbar at the top of the page */
  width: 100%; /* Full width */
  z-index: 100;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  display: inline-block;
  margin-left: 20px;
}

li a {
  text-decoration: none;
  font-size: 15px;
  color:  rgb(0, 255, 242);
};

li a:visited {
  color:  rgb(0, 255, 242);
}
@media only screen and (max-width: 992px) {
  .profile-links{
    color: white;
  }

  .profile-section{
    width: 100%;
    font-size: 1.2em;
    color: white;
    margin-top: 50px;
  }
}

@media only screen and (min-width: 991px) {
  .profile-links{
    color: white;
  }

  .profile-section{
    width: 35%;
    font-size: 1.2em;
    color: white;
    float: left;
    margin-top: 50px;
  }
}
.fade-enter {
  opacity: 0.01;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}

div.transition-group {
  position: relative;
}

section.route-section {
position: absolute;
width: 100%;
top: 0;
left: 0;
}
